<template>
  <div
    class="char"
    :class="show ? 'on' : 'off'"
    ref="char_ref"
    @mouseenter="emit('touch')"
  >
    {{ char }}
  </div>
</template>

<script setup>
import { watch, ref } from "vue";
const props = defineProps({
  char: String,
  ctrl: String,
});
const emit = defineEmits(["touch"]);

const show = ref(false);
const char_ref = ref(null);

const set_show = (s) => {
  show.value = s;
  // 重新刷新动画
  char_ref.value.style.animation = "none";
  char_ref.value.offsetHeight;
  char_ref.value.style.animation = "";
};

watch(
  () => props.ctrl,
  (value) => {
    set_show(value == "1");
  }
);
</script>

<style>
@keyframes moveup {
  from {
    background-color: #eee;
    color: #222;
  }
  to {
    background-color: #222;
    color: #eee;
  }
}

@keyframes movedown {
  to {
    background-color: #eee;
    color: #222;
  }
  from {
    background-color: #222;
    color: #eee;
  }
}

.off {
  animation: moveup 1s 1 both;
}

.on {
  animation: movedown 1s 1 both;
}

.char {
  cursor: default;
  user-select: none;
  padding: 6px min(10px, 2vw);
}
</style>